import {language, languageSwithing} from "./modules/language";
import nextBtn from "./modules/nextBtn";
import setHeroes from "./modules/setHeroes";

document.addEventListener('DOMContentLoaded', (event) => {

  try {
    language();
    setHeroes();
    nextBtn();
  } catch (error) {}

  languageSwithing();

  try {
    const scoreElem = document.querySelector('.results__score-points');
    const scoreValue = localStorage.getItem('score');
    scoreElem.innerHTML = scoreValue;
  } catch (error) {}

});